<template>
  <div>
    <svg-icon :icon="iconName(valid1)" width="14" height="14" /><span>{{ error1 }}</span
    ><br />
    <svg-icon :icon="iconName(valid2)" width="14" height="14" /><span>{{ error2 }}</span>
    <br />
    <svg-icon :icon="iconName(valid3)" width="14" height="14" /><span>{{ error3 }}</span>
    <br />
    <svg-icon :icon="iconName(valid4)" width="14" height="14" />
    <span>{{ error4 }}</span> <br />
  </div>
</template>
<script>
import { checkLength, pwdVerify, isNotSortString, checkFind } from '../utils'
export default {
  props: {
    password: {
      type: String,
      required: true,
    },
    userCode: {
      type: String,
      required: true,
    },
    error1: {
      type: String,
      required: true,
    },
    error2: {
      type: String,
      required: true,
    },
    error3: {
      type: String,
      required: true,
    },
    error4: {
      type: String,
      required: true,
    },
  },
  computed: {
    valid1() {
      return checkLength(this.password)
    },
    valid2() {
      return pwdVerify(this.password)
    },
    valid3() {
      return isNotSortString(this.password)
    },
    valid4() {
      return checkFind(this.userCode, this.password)
    },
  },
  methods: {
    iconName(valid) {
      return valid ? 'success' : 'stop'
    },
  },
}
</script>
<style lang="scss" scoped>
span {
  color: #666;
  margin-left: 5px;
  font-size: 14px;
  line-height: 20px;
}
</style>
