<template>
  <div class="login-box" @keyup.enter="submitForm">
    <div class="login-box-title">{{ $t('login.loginBoxTitle') }}</div>
    <div class="login-box-form">
      <el-form ref="loginForm" :model="loginForm" :rules="rules">
        <el-form-item prop="userIdentifier">
          <el-input
            v-model="loginForm.userIdentifier"
            :placeholder="emailPh()"
            size="medium"
          >
          </el-input>
        </el-form-item>
        <div v-if="loginForm.loginType === 1" class="login-box-form-step">
          <el-form-item prop="password">
            <el-input
              v-model="loginForm.password"
              type="password"
              show-password
              :placeholder="$t('login.passwordPh')"
              size="medium"
            >
            </el-input>
          </el-form-item>
          <el-form-item prop="imageVerifyCode">
            <div class="login-box-form-verify">
              <el-input
                v-model.trim="loginForm.imageVerifyCode"
                autocomplete="off"
                :placeholder="$t('login.imageCodePh')"
                size="medium"
              >
              </el-input>
              <img
                class="login-box-form-verify-img"
                :src="'data:image/png;base64,' + imgRes.img"
                @click="getVerifyId"
              />
            </div>
          </el-form-item>
        </div>
        <div v-else class="login-box-form-step">
          <el-form-item prop="imageVerifyCode">
            <div class="login-box-form-verify">
              <el-input
                v-model.trim="loginForm.imageVerifyCode"
                :placeholder="$t('login.imageCodePh')"
                size="medium"
              >
              </el-input>
              <img
                class="login-box-form-verify-img"
                :src="'data:image/png;base64,' + imgRes.img"
                @click="getVerifyId"
              />
            </div>
          </el-form-item>
          <el-form-item prop="emailVerifyCode">
            <div class="login-box-form-verify">
              <el-input
                v-model="loginForm.emailVerifyCode"
                :placeholder="$t('login.emailCodePh')"
                maxlength="8"
                size="medium"
              >
              </el-input>
              <el-button
                v-if="!countingDown"
                key="countingDown"
                :disabled="smsSendDisabled"
                class="login-box-form-verify-img"
                @click="sendEmailVerifyCode"
                >{{ getCodeStr }}</el-button
              >
              <el-button
                v-else
                key="countDownTime"
                :disabled="true"
                class="login-box-form-verify-img"
                >{{ $t('login.countDown', { countDownNum: countDownTime }) }}</el-button
              >
            </div>
          </el-form-item>
        </div>
        <el-form-item prop="checked">
          <div class="login-box-form-privacy">
            <el-checkbox v-model="loginForm.checked" size="mini"></el-checkbox>
            <div class="login-box-form-privacy-des">
              <span>{{ $t('login.privacyDesPart1') }}</span>
              <a class="agree-service" @click="open(0)">{{
                $t('login.privacyDesPart2')
              }}</a
              >{{ $t('login.privacyDesPart3') }}
              <a class="agree-service" @click="open(1)">{{
                $t('login.privacyDesPart4')
              }}</a>
            </div>
          </div>
        </el-form-item>
        <el-form-item>
          <el-button
            class="login-btn"
            type="primary"
            size="medium"
            :loading="loading"
            @click.native="submitForm"
            >{{ $t('login.loginButton') }}</el-button
          >
          <div class="login-box-type-row">
            <a class="login-box-type-row-button" @click="changeLoginType">
              {{ loginTypeStr }}
            </a>
            <a class="login-box-type-row-button" @click="clickForgetPassword">
              {{ $t('login.forgetPassword') }}
            </a>
          </div>
        </el-form-item>
      </el-form>
    </div>
  </div>
</template>
<script>
import fetch from '@/request/fetch'
import loginApi from '@/request/modules/login'
import * as crypto from '@/utils/cryptojs'
import { MessageBox } from 'element-ui'
export default {
  data() {
    return {
      hasSentCode: false,
      smsSendDisabled: false,
      countDownTime: 60,
      countingDown: false,
      loading: false,
      imgRes: {
        img: '',
        verifyId: '',
      },
      passVisible: true,
      imageVisible: false,
      loginForm: {
        password: '',
        userIdentifier: '',
        emailVerifyCode: '',
        imageVerifyCode: '',
        verifyId: '',
        loginType: 1,
      },
    }
  },
  computed: {
    loginTypeStr() {
      return this.loginForm.loginType === 1
        ? this.$t('login.loginTypeEmail')
        : this.$t('login.loginTypePassword')
    },
    getCodeStr() {
      return this.hasSentCode ? this.$t('login.reSend') : this.$t('login.getCode')
    },
    rules() {
      return {
        userIdentifier: [
          { required: true, message: this.emailPh(), trigger: 'blur' },
          { validator: this.validateUserIdentifier, trigger: 'blur' },
        ],
        password: [{ required: true, message: this.$t('login.passwordPh') }],
        checked: [{ validator: this.checkIsChecked, trigger: 'blur' }],
        imageVerifyCode: [
          { required: true, message: this.$t('login.imageCodePh'), trigger: 'blur' },
        ],
        emailVerifyCode: [
          { required: true, message: this.$t('login.emailCodePh'), trigger: 'blur' },
        ],
      }
    },
  },
  watch: {
    '$i18n.locale': {
      handler() {
        this.$nextTick(() => this.$refs.loginForm.clearValidate())
      },
    },
  },
  mounted() {
    this.getVerifyId()
    this.loginDetect()
  },
  methods: {
    checkIsChecked(rule, value, callback) {
      if (!value) {
        return callback(this.$t('login.checkPrivacyPh'))
      }
      callback()
    },
    emailPh() {
      return this.loginForm?.loginType === 1
        ? this.$t('login.loginType1EmailPh')
        : this.$t('login.loginType2EmailPh')
    },
    open(index) {
      const urlStr =
        this.$i18n.locale === 'zh'
          ? 'https://www.chinatelecomglobal.com/sc/footer/columns/2492?index='
          : 'https://www.chinatelecomglobal.com/footer/columns/2492?index='

      window.open(urlStr + index)
    },
    clickForgetPassword() {
      this.$router.push('forgetPassword')
    },
    changeLoginType() {
      if (this.loginForm.loginType === 1) {
        this.loginForm.loginType = 2
      } else {
        this.loginForm.loginType = 1
      }
      this.$nextTick(() => this.$refs.loginForm.clearValidate())
    },
    validatePassword(rule, value, callback) {
      if (!value) {
        callback(new Error(this.$t('login.passwordPh')))
        return
      }
      const pattern =
        /^(?![A-z0-9]+$)(?=.[^%&',;=?$\x22])(?=.*[a-z])(?=.*[A-Z])(?=.*[0-9]).{8,20}$/

      if (!pattern.test(value)) {
        callback(new Error('请输入8位到20位包含英文大小写,数字,特殊字符'))
        return
      }
      callback()
    },
    loginDetect() {
      loginApi.login.getCurrent().then((user) => {
        if (user && user.userCode) {
          MessageBox.confirm(
            '检测到已经登录账号[' + user.userCode + ']，是否跳转首页？',
            '提示',
            {
              confirmButtonText: '确定',
              cancelButtonText: '注销',
              type: 'warning',
            }
          )
            .then(() => {
              // window.location.href = '/'
              this.$router.replace('/')
            })
            .catch(() => {})
        }
      })
    },
    // 获取图片验证码序列
    async getVerifyId() {
      this.imgRes = await fetch('/verifyCode/getVerifyCode', {}, { method: 'get' })
    },
    // 发送邮件验证码
    async sendEmailVerifyCode() {
      const { userIdentifier, imageVerifyCode } = this.loginForm
      if (userIdentifier && imageVerifyCode) {
        this.smsSendDisabled = true
        fetch(
          '/verifyCode/getEmailVerifyCode',
          { email: userIdentifier, imageVerifyCode, verifyId: this.imgRes.verifyId },
          { method: 'get' }
        )
          .then(() => {
            this.$message.success(`发送成功！`)
            this.startCountDown()
          })
          .catch(() => (this.smsSendDisabled = false))
      }
    },
    startCountDown() {
      this.countingDown = true
      const countDownInterval = setInterval(() => {
        if (this.countDownTime > 1) {
          this.countDownTime--
        } else {
          clearInterval(countDownInterval)
          this.resetCountDown()
        }
      }, 1000)
    },
    resetCountDown() {
      this.hasSentCode = true
      this.countingDown = false
      this.smsSendDisabled = false
      this.countDownTime = 60
    },
    // 手机号码校验
    validateUserIdentifier(rule, value, callback) {
      if (this.loginForm.loginType === 2) {
        if (!(value.includes('@') && value.includes('.') && value.length < 61)) {
          callback(new Error('请输入正确的邮箱地址'))
        }
      }
      callback()
    },
    async submitForm() {
      this.$refs.loginForm.validate(async (valid) => {
        if (valid) {
          const params = { ...this.loginForm, verifyId: this.imgRes.verifyId }
          params.password = encodeURIComponent(
            crypto.EncryptPkcs7(params.password, 'zhuyu&whh1234567')
          )
          this.loading = true
          const res = await fetch('/verifyAndLoginUser', { ...params }, { method: 'get' })
            .catch(() => {
              this.getVerifyId()
            })
            .finally(() => (this.loading = false))
          if (res === '/') {
            localStorage.setItem('bulletinHasPop', 0) // 登录后允许首页弹窗
            this.$router.replace('/')
          }
        }
        return valid
      })
    },
  },
}
</script>

<style lang="scss" scoped>
@import '@/assets/css/login.scss';
</style>
