var render = function render(){var _vm=this,_c=_vm._self._c;return _c('div',{staticClass:"center-dialog"},[_c('el-dialog',{attrs:{"top":"1vh","visible":_vm.v,"append-to-body":false,"width":"55%","modal":true,"close-on-click-modal":true},on:{"update:visible":function($event){_vm.v=$event}}},[_c('div',{attrs:{"slot":"title"},slot:"title"},[_c('span',{staticClass:"cor-baseTextColor"},[_vm._v("隐私政策")])]),_c('div',[_c('div',{staticClass:"notice-wrap"},[_c('header',{staticClass:"notice-header"},[_c('h1',[_vm._v(_vm._s(_vm.notice.title))])]),_c('article',{staticClass:"main-content"},[_c('pre',[_vm._v("尊敬的用户:\n\n中国电信物联网连接管理平台由天翼物联科技有限公司（简称 “我们”）创建和运营，我们的注册地址为：江苏省南京市秦淮区中山南路501号1101室。为方便用户（简称“您”）注册和使用物联网连接管理平台，我们可能会收集和使用您的相关数据和信息（统称“信息”或“用户信息”）。我们严格按照《网络安全法》、《全国人民代表大会常务委员会关于加强网络信息保护的决定》、《电信和互联网用户客户信息保护规定》、《电话用户真实身份信息登记规定》法律法规的相关要求，对提供服务过程中收集、使用的客户信息履行保护义务。我们将通过《中国电信物联网连接管理平台隐私政策》（简称“本隐私政策”）向您说明我们如何收集和使用您的信息，请您在注册和使用平台服务之前认真阅读、充分理解本隐私政策，尤其是划线和加粗的内容。如果您对本隐私政策有疑问的，可通过本隐私政策约定的方式询问，我们将解释本隐私政策内容。如您不同意本隐私政策或其中任何条款，您可选择停止注册或注销平台帐户，同时您应知晓，平台将无法再为您提供相关服务。\n\n本政策将帮助您了解以下内容\n一、 隐私政策适用范围\n二、 我们如何收集和使用您的个人信息\n三、 我们如何使用 Cookie\n四、 信息存储\n五、 信息共享、转移、公开披露\n六、 信息保护\n七、 信息管理\n八、 我们如何处理未成年人的个人信息\n九、 征得授权同意的例外\n十、 隐私政策的修订\n十一、 联系方式\n\n一、隐私政策适用范围\n1.1 本隐私政策适用于平台所有服务。\n1.2 第三方企业使用物联网平台提供的服务，收集和使用您的信息的，适用您和第三方之间关于个人信息和隐私的约定。\n\n二、我们如何收集和使用您的个人信息\n2.1 企业用户基本信息\n企业客户需在中国电信CRM系统进行实名认证，然后联系客户经理申请开通平台账号，平台不提供自主注册功能，平台使用企业手机及邮箱信息，用于忘记密码、修改个人联系信息的验证操作。\n2.2 除另有说明外，我们不会收集您使用服务所存储在平台内的任何数据信息。\n2.3 如您拒绝提供相应信息，将无法开通连接管理平台账号，无法使用连接管理平台功能。\n\n三、我们如何使用 Cookie\n我们会通过 Cookies 和同类技术收集和使用您的信息。我们对Cookie的使用政策为：\n3.1 为确保平台正常运转，我们会在您的计算机上或移动设备上存储、设定或取用Cookies, Cookies是存储于您硬盘或设备内存中的小型数据文件，借助于 Cookies，我们能存储您的偏好（如计算机或移动设备的语言、字体大小和其他浏览偏好）。\n3.2 您可以通过自行修改浏览器设置的方式拒绝或管理Cookies。如果您关闭Cookies的使用或删除存储的Cookies后，则需要在每一次使用平台时更改用户设置。\n\n四、信息存储\n4.1 平台收集的有关您的信息将保存在天翼云（由中国电信股份有限公司云计算分公司提供的云服务，包括但不限于：基于云计算资源、云存储资源、云网络资源、云安全资源、云管理与部署资源、云数据库资源、专属云、专用物理机资源互联网接入等所提供的弹性云主机、弹性伸缩服务、专用物理机、云镜像、弹性块存储、对象存储、云备份、虚拟私有云、弹性负载均衡、云专线接入、云加速、关系型数据库、DDos防护 、云监控、专享云主机等。）在中华人民共和国境内（为本隐私政策之目的，不含香港、澳门、台湾地区）的服务器上。如在符合适用法律规定的情形下因业务需要向境外传输个人信息的，我们会事先征得您的同意，向您告知用户信息出境的目的、接收方等情况，并履行相关法律法规规定的程序。\n4.2 对于所收集的信息，我们将在法律规定的最短期限内保存。在您的信息已经不再需要用于实现本隐私政策规定的目的和用途，也无需根据相关法律法规的规定保存时，我们将采取合理步骤以安全的方式销毁个人信息或使其不可识别并不可被再次编辑、修改、使用。\n\n五、信息共享、转移、公开披露\n5.1 共享\n未经您事先同意，我们不会与任何第三方共享您的信息，但以下情形除外：\n（1）在获取明确同意的情况下共享：经您事先明确同意，我们会与其他方共享您的信息；\n（2）根据相关法律法规的规定，或者行政、司法机关的要求，向第三方或者行政、司法机关披露您的信息；\n（3）与授权合作伙伴共享：为提供服务之需，我们的某些服务将由授权合作伙伴提供。例如，为进行实名认证，我们会将您提供的身份信息向授权合作伙伴提供，以确认您的真实身份。我们委托的第三方合作伙伴包括供应商、技术服务商、产品服务商、客户服务商等。我们仅会根据合法、正当、必要的原则，基于特定、明确的目的与授权合作伙伴共享您的用户信息，仅会共享提供服务所必要的用户信息，并要求合作伙伴按照本隐私政策以及其他任何相关的保密和安全措施来处理您的信息。\n（4）为向您提供服务之需，我们可能会向关联方中国电信披露您的信息。\n对我们与之共享您的信息的组织和个人，我们会与其签署严格的保密协定，要求他们按照我们的说明、本隐私政策以及其他任何相关的保密和安全措施来处理您的信息。\n5.2 转移\n我们不会将您的个人信息转移给任何公司、组织和个人，但以下情况除外：\n（1）在获取明确同意的情况下转让：获得您的明确同意后，我们会向其他方转移您的个人信息；\n（2）在涉及合并、收购或破产清算时，如涉及到个人信息转让，我们会要求新的持有您的信息的公司、组织继续受此隐私政策的约束，否则我们将要求该公司、组织重新向您征求授权同意。\n5.3 公开披露我们仅会在以下情况下，公开披露您的个人信息：\n（1）获得您明确同意后；\n（2）基于法律的披露：在法律、法律程序、诉讼或政府主管部门强制性要求的情况下，我们可能会公开披露您的个人信息。\n\n六、信息保护\n6.1 我们对用户信息的保护\n我们非常重视您的信息安全。我们采用适当的物理、管理和技术保障措施来防止您的信息遭到未经授权访问、披露、使用、修改、损坏或丢失。\n例如，我们已建立相关内控制度，对可能接触到您信息的工作人员采取最小够用授权原则；我们会使用混合加密技术提高信息的保密性；我们会使用受信赖的保护机制防止您的信息遭到恶意攻击；我们会部署访问控制机制，确保只有授权人员才可访问用户信息；我们会举办安全和培训，加强员工对于保护用户信息重要性的认识。\n但请您理解，由于技术的限制以及可能存在的各种恶意手段，在互联网环境下，即便竭尽所能加强安全措施，也不可能始终保证信息百分之百的安全。若不幸发生信息泄露等安全事件，我们会立即启动应急预案，阻止事件扩大，及时采取补救措施，并推送通知、公告等多种形式告知您。\n\n6.2 用户对信息的保管\n尽管有前述安全措施，但请您使用复杂密码，协助我们保证您的账号安全；并妥善保管您的账户、密码及其他信息，避免您的个人信息泄露。如果您发现您的账号、密码或其他信息被他人非法使用或有使用异常的情况的，应及时通知我们，我们将采取相应安全保障措施。\n\n七、信息管理\n7.1 查询\n您可以在连接管理平台页面查询与管理企业客户基本信息。\n7.2 变更\n您应确保向我们提交的所有个人数据都准确无误。如果您发现我们对您的信息收集、存储有错误，您可以通过在4008285656提交工单、联系业务受理渠道等方式要求我们对相关信息予以更正。\n7.3 删除\n若您发现我们未按照法律法规的规定或者本隐私政策约定收集和使用您的信息，您可以通过工单、邮件等方式联系我们对相关信息（包含副本、数据备份）进行删除。\n7.4 改变或撤回您的授权信息\n对于您已经授权同意我们收集和使用的终端设备权限信息，您可以通过在终端设备进行设置、改变或撤回您的授权同意；您改变或撤回授权同意后，可能无法使用相应的功能服务。\n7.5 注销账户\n您可以通过4008285656提交工单，或通过联系客户经理申请注销账户，核实账户信息后，我们将会在5个工作日内为您完成销户。\n\n您的平台账户注销后，您账户内的所有信息将被清空，我们将不会继续收集、使用或共享与该账户相关的个人信息。对于账户注销前已经收集的信息，我们将按照相关法律法规规定及国家监管要求进行强加密保存，并可能在该依法保存期间受依据有权机关指令、提供给有权机关查询，保存期限及期限届满后的处理方式见 本隐私政策“信息存储”章节。\n7.6 为保障安全，您申请查询、变更、删除您的信息或注销账户时，可能需要提供书面请求，或以其他方式证明您的身份。我们可能会先要求您验证自己的身份，然后再处理您的请求。\n7.7 如果我们停止运营某一产品或者服务，我们将及时停止收集和使用该产品或服务所需的您的个人信息，同时将停止运营的通知通过平台公告进行发布，同时以短信、邮件等渠道告知您。对于该产品或服务运营期间已经收集的您的个人信息，我们将依法保存，并在保存期限届满后进行匿名化处理或删除。\n7.8 如果我们发生收购、兼并、重组等变更，会将变更信息通过平台页面进行发布。变更后我们会继续履行信息保护的责任和义务，如果个人信息使用目的有所变化，我们将明确提示并在您授权同意后才会获取权限，如未取得您的授权，我们将不会收集和使用相关信息。\n7.9 尽管有上述约定，但按照法律法规要求，在以下情形下，我们可能无法响应您的请求：\n（1）与国家安全、国防安全有关的；\n（2）与公共安全、公共卫生、重大公共利益相关的；\n（3）与犯罪侦查、起诉和审判等有关的；\n（4）有充分证据表明您存在主观恶意或滥用权力的；\n（5）响应您的请求将导致其他个人、组织的合法权益受到严重损害的。\n\n八、 我们如何处理未成年人的个人信息、\n8.1 我们非常重视对未成年人个人信息的保护。若您是14周岁以下的未成年人，将无法通过企业用户实名并使用连接管理平台。\n8.2 如果我们发现在未事先获得可证实的父母或法定监护人同意的情况下收集了未成年人的个人信息，会设法尽快删除相关数据。\n\n九、征得授权同意的例外\n您充分理解并同意，我们在以下情况下收集、使用您的个人信息无需您的授权同意，且我们可能不会响应您提出的更正/修改、删除、注销、撤回同意、索取信息的请求：\n9.1 与国家安全、国防安全有关的。\n9.2 与公共安全、公共卫生、重大公共利益有关的。\n9.3 与犯罪侦查、起诉、审判和判决执行等司法或行政执法有关的。\n9.4 出于维护您或其他个人的生命、财产等重大合法权益但又很难得到本人同意的。\n9.5 您自行向社会公众公开的个人信息。\n9.6从合法公开披露的信息中收集个人信息的，如合法的新闻报道、政府信息公开等渠道。\n9.7 根据与您签订和履行相关协议或其他书面文件所必需的。\n9.8 用于维护所提供的产品及/或服务的安全稳定运行所必需的，包括但不限于发现、处置产品及/或服务的故障。\n9.9 为合法的新闻报道所必需的。\n9.10 学术研究机构基于公共利益开展统计或学术研究所必要，且对外提供学术研究或描述的结果时，对结果中所包含的个人信息进行去标识化处理的。\n9.11 法律法规规定的其他情形。\n\n十、隐私政策的修订\n10.1 基于为您提供更好的服务的目的，并根据电信业务的发展或法律法规及监督政策变化的要求，我们可能会适时修订本隐私政策，修订后的隐私政策作为合同附件提供给第三方企业。对于本隐私政策的重大变更，我们会请您重新授权同意，如果您不同意新的隐私政策则无法使用本服务。您可以通过省客户经理反馈。\n\n十一、联系方式\n11.1 如您对本隐私政策或您个人信息的相关事宜有任何问题、意见、建议或申诉，您可以通过拨打我们的客服电话4008285656与我们取得联系，我们将与连接管理平台个人信息保护负责人进一步沟通。\n11.2 为保障安全，您可能需要提供书面请求，或以其他方式证明您的身份。一般情况下，一般情况下，我们将会在3个工作日内回复，特殊情形下，最长将在不超过7天或法律规定期限内做出答复。\n11.3 如果您对我们的回复不满意，特别是我们的个人信息处理行为损害了您的合法权益，您还可以向网信、电信、公安及市监等监管部门进行投诉或举报。\n              ")])])])])])],1)
