/* eslint-disable no-useless-escape */
const ORDER_STRING = [
  '~!@#$%^&*()_+',
  '`1234567890-=',
  'qwertyuiop[]\\',
  'QWERTYUIOP{}|',
  "asdfghjkl;'",
  'ASDFGHJKL:"',
  'zxcvbnm,./',
  'ZXCVBNM<>?',
]

const MAX_SORT = 3
const PASSWORD_MIN_LENGTH = 8
const PASSWORD_MAX_LENGTH = 16

export function checkLength(pwd) {
  return pwd.length >= PASSWORD_MIN_LENGTH && pwd.length <= PASSWORD_MAX_LENGTH
}

export function repeat(pwd) {
  pwd = pwd.toLowerCase()
  for (let i = 0; i < pwd.length - MAX_SORT + 1; i++) {
    if (
      pwd
        .slice(i, i + MAX_SORT)
        .split('')
        .every((c, _, arr) => c === arr[0])
    ) {
      return false
    }
  }
  return true
}

export function pwdVerify(pwd) {
  const pwd09 = /[0-9]/
  const pwdaz = /[a-z]/
  const pwdAZ = /[A-Z]/
  const pwdSymbol = /[!@#\$%\^\&*\)\(+=._-]/

  let flag = 0
  if (pwd09.test(pwd)) flag++
  if (pwdaz.test(pwd)) flag++
  if (pwdAZ.test(pwd)) flag++
  if (pwdSymbol.test(pwd)) flag++

  return flag >= 3
}

export function isNotSortString(pwd) {
  for (const string of ORDER_STRING) {
    for (let i = 0; i <= string.length - MAX_SORT; i++) {
      const substring = string.slice(i, i + MAX_SORT)
      if (pwd.includes(substring)) {
        return false
      }
    }
  }
  return true
}

export function checkFind(account, pwd) {
  account = account.toLowerCase()
  pwd = pwd.toLowerCase()
  return !pwd.includes(account)
}

export function validatePassword(password) {
  if (!checkLength(password)) {
    console.error('密码长度不足')
    return false
  }
  if (!repeat(password)) {
    console.error('出现连续重复字符')
    return false
  }
  if (!pwdVerify(password)) {
    console.error('密码强度不够')
    return false
  }
  if (!isNotSortString(password)) {
    console.error('存在键盘排序')
    return false
  }

  return true
}

// Example usage
const input1 = 'Password123!' // Valid
const input2 = '12345678' // Invalid
const input3 = 'qwerty' // Invalid
const input4 = 'Aa1!Aa1!' // Valid
const input5 = 'aaa123' // Invalid

console.log(validatePassword(input1)) // true
console.log(validatePassword(input2)) // false
console.log(validatePassword(input3)) // false
console.log(validatePassword(input4)) // true
console.log(validatePassword(input5)) // false
