<template>
  <div class="forget-box" @keyup.enter="submitForm">
    <div class="steps-container">
      <CtSteps
        :total-steps="stepsLabel.length"
        :current-step="currentStep"
        :steps-label="stepsLabel"
      />
    </div>
    <ForgetSuccess v-if="currentStep === 2"></ForgetSuccess>
    <div v-else>
      <div class="forget-box-title">{{ title }}</div>
      <div class="forget-box-form">
        <el-form ref="forgetForm" :model="forgetForm" :rules="rules">
          <div v-if="currentStep === 0" class="forget-box-form-step1">
            <el-form-item prop="email">
              <el-input
                v-model="forgetForm.email"
                :placeholder="$t('login.loginType2EmailPh')"
                size="medium"
              >
              </el-input>
            </el-form-item>
          </div>
          <div v-if="currentStep === 1" class="forget-box-form-step2">
            <el-form-item prop="userCode">
              <el-input v-model="forgetForm.userCode" disabled size="medium"> </el-input>
            </el-form-item>
            <el-tooltip placement="right" effect="light" popper-class="password-poper">
              <div slot="content">
                <PasswordTip
                  :password="forgetForm.password1"
                  :user-code="forgetForm.userCode"
                  :error1="$t('forgetPassword.passwordError1')"
                  :error2="$t('forgetPassword.passwordError2')"
                  :error3="$t('forgetPassword.passwordError3')"
                  :error4="$t('forgetPassword.passwordError4')"
                />
              </div>
              <el-form-item prop="password1">
                <el-input
                  v-model="forgetForm.password1"
                  type="password"
                  show-password
                  :placeholder="$t('forgetPassword.passwordPh')"
                  size="medium"
                  @blur="password1Blur"
                >
                </el-input>
              </el-form-item>
            </el-tooltip>
            <el-form-item prop="password2">
              <el-input
                v-model="forgetForm.password2"
                type="password2"
                show-password
                :placeholder="$t('forgetPassword.password2Ph')"
                size="medium"
              >
              </el-input>
            </el-form-item>
            <el-form-item prop="imageVerifyCode">
              <div class="forget-box-form-verify">
                <el-input
                  v-model.trim="forgetForm.imageVerifyCode"
                  autocomplete="off"
                  :placeholder="$t('login.imageCodePh')"
                  size="medium"
                >
                </el-input>
                <img
                  class="forget-box-form-verify-img"
                  :src="'data:image/png;base64,' + imgRes.img"
                  @click="getVerifyId"
                />
              </div>
            </el-form-item>
            <el-form-item prop="emailVerifyCode">
              <div class="forget-box-form-verify">
                <el-input
                  v-model="forgetForm.emailVerifyCode"
                  :placeholder="$t('login.emailCodePh')"
                  maxlength="8"
                  size="medium"
                >
                </el-input>
                <el-button
                  v-if="!countingDown"
                  :disabled="smsSendDisabled"
                  class="forget-box-form-verify-img"
                  @click="sendEmailVerifyCode"
                  >{{ sendCodButMain }}</el-button
                >
                <el-button v-else :disabled="true" class="forget-box-form-verify-img">{{
                  $t('login.countDown', { countDownNum: countDownTime })
                }}</el-button>
              </div>
            </el-form-item>
          </div>
          <el-form-item>
            <el-button
              class="login-btn"
              type="primary"
              size="medium"
              :loading="loading"
              @click="submitForm"
              >{{ $t('forgetPassword.next') }}</el-button
            >
          </el-form-item>
          <div class="tips">
            <span>{{ $t('forgetPassword.hasAccount') }}</span>
            <a class="tips-login" @click="toLogin">{{
              $t('forgetPassword.imediateLogin')
            }}</a>
          </div>
        </el-form>
      </div>
    </div>
  </div>
</template>
<script>
import PasswordTip from './PasswordTip'
import fetch from '@/request/fetch'
import CtSteps from '@/components/CtSteps'
import ForgetSuccess from './ForgetSuccess'
import * as crypto from '@/utils/cryptojs'
import { checkLength, pwdVerify, isNotSortString, checkFind } from '../utils'
export default {
  components: {
    CtSteps,
    ForgetSuccess,
    PasswordTip,
  },
  data() {
    return {
      currentStep: 0,
      sendCodButMain: this.$t('login.getCode'),
      smsSendDisabled: false,
      countDownTime: 60,
      countingDown: false,
      loading: false, // 登陆状态
      imgRes: {
        img: '',
        verifyId: '',
      },
      pwdShow: false,
      codeImg: '',
      passVisible: true,
      imageVisible: false,
      smsVisible: false,
      forgetForm: {
        // 登陆表单
        email: '',
        password1: '',
        password2: '',
        userCode: '',
        emailVerifyCode: '',
        imageVerifyCode: '',
        verifyId: '', // 图片验证码序列
      },
    }
  },
  computed: {
    rules() {
      return {
        email: [
          {
            required: true,
            message: this.$t('login.loginType2EmailPh'),
            trigger: 'blur',
          },
        ],
        password1: [
          { required: true, message: this.$t('login.passwordPh') },
          { validator: this.validatePassword, trigger: 'blur' },
        ],
        password2: [
          { required: true, message: this.$t('login.passwordPh') },
          { validator: this.validatePassword2, trigger: 'blur' },
        ],
        imageVerifyCode: [
          { required: true, message: this.$t('login.imageCodePh'), trigger: 'blur' },
        ],
        emailVerifyCode: [
          { required: true, message: this.$t('login.emailCodePh'), trigger: 'blur' },
        ],
      }
    },
    stepsLabel() {
      return [
        this.$t('forgetPassword.validProfile'),
        this.$t('forgetPassword.resetPassword'),
        this.$t('forgetPassword.finished'),
      ]
    },
    title() {
      return this.stepsLabel[this.currentStep]
    },
  },
  mounted() {
    this.getVerifyId()
  },
  methods: {
    password1Blur() {
      if (this.forgetForm.password2?.length > 0) {
        this.$refs.forgetForm.validateField('password2')
      }
    },
    toLogin() {
      this.$router.replace('login')
    },
    clickForgetPassword() {
      this.smsVisible = true
    },
    async getVerifyId() {
      this.imgRes = await fetch('/verifyCode/getVerifyCode', {}, { method: 'get' })
    },
    // 发送邮件验证码
    async sendEmailVerifyCode() {
      const { email, imageVerifyCode } = this.forgetForm
      if (email && imageVerifyCode) {
        this.smsSendDisabled = true
        fetch(
          '/verifyCode/getEmailVerifyCode',
          { email, imageVerifyCode, verifyId: this.imgRes.verifyId },
          { method: 'get' }
        )
          .then(() => {
            this.$message.success(this.$t('forgetPassword.sendSuccess'))
            this.startCountDown()
          })
          .catch(() => (this.smsSendDisabled = false))
      }
    },
    startCountDown() {
      this.countingDown = true
      const countDownInterval = setInterval(() => {
        if (this.countDownTime > 1) {
          this.countDownTime--
        } else {
          clearInterval(countDownInterval)
          this.resetCountDown()
        }
      }, 1000)
    },
    resetCountDown() {
      this.sendCodButMain = this.$t('login.reSend')
      this.countingDown = false
      this.smsSendDisabled = false
      this.countDownTime = 60
    },
    refreshImg() {
      this.imageSrc =
        this.iamHost +
        '/services/auth/CodeImage?loginId=' +
        this.forgetForm.userCode +
        '&t=' +
        Math.random()
    },
    async submitForm() {
      await this.$refs.forgetForm.validate()
      // 第一步 获取用户名
      if (this.currentStep === 0) {
        this.loading = true
        const userCode = await fetch(
          '/user/queryByEmail',
          { email: this.forgetForm.email },
          { method: 'get' }
        ).finally(() => (this.loading = false))
        if (userCode.length > 0) {
          this.forgetForm.userCode = userCode
          // 前进
          this.currentStep++
        }
      } else if (this.currentStep === 1) {
        // 第二步 设置密码
        const params = { ...this.forgetForm, verifyId: this.imgRes.verifyId }
        params.password1 = crypto.EncryptPkcs7(params.password1, 'zhuyu&whh1234567')
        params.password2 = crypto.EncryptPkcs7(params.password2, 'zhuyu&whh1234567')
        fetch('/forgetPassword', { ...params }, { method: 'post' })
          .catch(() => {
            this.getVerifyId()
          })
          .then(() => this.currentStep++)
          .finally(() => (this.loading = false))
      }
    },
    validatePassword(rule, value, callback) {
      if (!checkLength(value)) {
        callback(new Error(this.$t('forgetPassword.passwordError1')))
        return
      }
      if (!pwdVerify(value)) {
        callback(new Error(this.$t('forgetPassword.passwordError2')))
        return
      }
      if (!isNotSortString(value)) {
        callback(new Error(this.$t('forgetPassword.passwordError3')))
        return
      }
      if (!checkFind(this.forgetForm.userCode, value)) {
        callback(new Error(this.$t('forgetPassword.passwordError4')))
        return
      }
      callback()
    },
    validatePassword2(rule, value, callback) {
      if (value != this.forgetForm.password1) {
        callback(new Error(this.$t('forgetPassword.notSame')))
        return
      }
      callback()
    },
  },
}
</script>

