    color: #333;
    margin-top: 10px;
    text-align: center;
  }

  &-form {
    padding: 30px 230px 20px 230px;
    box-sizing: border-box;

    ::v-deep .el-form-item {
      .el-input__inner {
        height: 48px;
      }
      .el-form-item__error {
        padding-top: 8px;
        font-size: 14px;
      }
      .el-input__inner {
        border-radius: 3px;
      }
    }

    //图片验证码
    &-verify {
      display: flex;
      width: 100%;
      ::v-deep .el-input {
        flex: 1;
      }
      &-img {
        margin-left: 20px;
        width: 110px;
        height: 48px;
        border-radius: 3px;
      }
    }
    //短信发送
    &-sms {
      display: flex;
      width: 100%;
      ::v-deep .el-input {
        flex: 1;
      }
      &-btn {
        margin-left: 15px;
        width: 83px;
        height: 36px;
        border-radius: 3px;
        padding: 0;
      }
      //短信验证码
      ::v-deep .el-input__inner {
        padding-left: 5px !important;
      }
    }

    .login-btn {
      background: linear-gradient(-90deg, #6e91f6, #2255ea);
      width: 100%;
      height: 48px;
      border-radius: 3px;
      &:hover {
        background: linear-gradient(-90deg, #82a1fb, #3666f4);
      }
    }
  }
}
.steps-container {
  padding: 30px 70px;
  display: flex;
}
.tips {
  font-weight: 500;
  font-size: 14px;
  color: #333333;
  text-align: center;
  &-login {
    color: #3262ed;
  }
}
</style>
